import React, { useState } from "react";
import Cart from "../cart/Cart";
import Button from "../button/Button";
import Previos from "../previos/Previos";
import Next from "../next/Next";
import styl from "../main/Main.module.scss";
import Modal from "../modal/Modal";

const Main = ({ users, getUserPosts, toggle, isOpen, posts }) => {
  const [page, setPage] = useState(1);
  const num = (page - 1) * 4;

  const increment = () => {
    if (page === Math.round(users.length / 4)) {
      setPage(1);
      return;
    }
    setPage(page + 1);
  };
  const decrement = () => {
    if (page < 2) {
      setPage(Math.round(users.length / 4));
      return;
    }
    setPage(page - 1);
  };

  return (
    <>
      <ul className={!isOpen ? styl.mainnormaldiv : styl.mainsmalldiv}>
        {users.slice(0 + num, 4 + num).map((user) => (
          <li
            key={user.id}
            className={!isOpen ? styl.mainnormal : styl.mainsmall}
          >
            <Cart user={user} />
            <Button
              text="Show all posts"
              toggle={() => getUserPosts(user.id)}
              style={`button`}
            />
          </li>
        ))}
      </ul>
      {isOpen && (
        <Modal toggle={toggle}>
          <ul className={styl.modalList}>
            {posts.map((userpost) => (
              <li key={userpost.id}>
                <p className={styl.modaltitle} key={userpost.title}>
                  {userpost.title}
                </p>
                <p className={styl.modaltext} key={userpost.body}>
                  {userpost.body}
                </p>
                <hr />
              </li>
            ))}
          </ul>
        </Modal>
      )}
      <footer className={styl.footer}>
        <div className={styl.previos}>
          <Previos />
          <Button
            style={`previous`}
            text="Previous"
            toggle={decrement}
          ></Button>
        </div>
        <div className={styl.previos}>
          <Button text="Next" toggle={increment} style={`next`}></Button>
          <Next />
        </div>
      </footer>
    </>
  );
};

export default Main;
